import { createStore } from "vuex";

export default createStore({
  state: {
    address: "",
    menuActive: sessionStorage.getItem("active") || "/home",
    outerWidth: "",
    isLoading: false,
    language: localStorage.getItem("language") || "en_US",
  },
  getters: {
    getAddress(state) {
      const address = sessionStorage.getItem("address");
      return address || state.address;
    },
    getMnuActive(state) {
      return state.menuActive;
    },
    getInnerWidth(state) {
      return state.outerWidth;
    },
    getIsLoading(state) {
      return state.isLoading;
    },
    getLanguage(state) {
      return state.language;
    },
  },
  mutations: {
    setMnuActive(state, data) {
      state.menuActive = data;
    },
    setAddress(state, data) {
      sessionStorage.setItem("address", data);
      state.address = data;
    },
    setInnerWidth(state, data) {
      sessionStorage.setItem("outerWidth", data);
      state.outerWidth = data;
    },
    setIsLoading(state, data) {
      state.isLoading = data;
    },
    setLanguage(state, data) {
      state.language = data;
    },
  },
  actions: {
    setMnuActive(context, data) {
      context.commit("setMnuActive", data);
    },
    setAddress(context, data) {
      context.commit("setAddress", data);
    },
    setInnerWidth(context, data) {
      context.commit("setInnerWidth", data);
    },
    setIsLoading(context, data) {
      context.commit("setIsLoading", data);
    },
    setLanguage(context, data) {
      localStorage.setItem("language", data);
      context.commit("setLanguage", data);
    },
  },
  // modules: {},
});
