import { createRouter, RouteRecordRaw, createWebHashHistory } from "vue-router";

const routes: Array<RouteRecordRaw> = [
  {
    path: "/",
    name: "layout",
    component: () =>
      import(/* webpackChunkName: "about" */ "@/views/Layout.vue"),
    redirect: "/home",
    children: [
      {
        path: "/home",
        name: "Home",
        component: () =>
          import(/* webpackChunkName: "about" */ "@/views/Home.vue"),
      },
      {
        path: "/whitePaper",
        name: "White Paper",
        component: () =>
          import(/* webpackChunkName: "about" */ "@/views/WhitePaper.vue"),
      },
      {
        path: "/majicArk",
        name: "Majic Ark",
        component: () =>
          import(/* webpackChunkName: "about" */ "@/views/MajicArk.vue"),
      },
      {
        path: "/seaOfFreedom",
        name: "Sea Of freedom",
        component: () =>
          import(/* webpackChunkName: "about" */ "@/views/SeaOfFreedom.vue"),
      },
      {
        path: "/captainRecruitment",
        name: "Captain Recruitment",
        component: () =>
          import(
            /* webpackChunkName: "about" */ "@/views/CaptainRecruitment.vue"
          ),
      },
    ],
  },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
});

router.beforeEach((to, from, next) => {
  const layoutDom = document.querySelector(".layout");
  if (layoutDom) {
    layoutDom.scrollTo(0, 0);
  }
  sessionStorage.setItem("active", to.path);
  next();
});

export default router;
