import { createI18n } from "vue-i18n";

import store from "@/store/index";

import zhCN from "./lang/zh_CN";

import enUS from "./lang/en_US";

const i18n = createI18n({
  globalInjection: true, // 全局注册 $t方法
  locale: store.getters.getLanguage,

  messages: {
    zh_CN: zhCN, // 中文
    en_US: enUS, // 英文
  },
});

export default i18n;
